import { Component} from '@angular/core';
import { Router } from '@angular/router';
import { Http } from '../../node_modules/@angular/http';

declare var xaa : any;

@Component({
  selector: 'app-cyber-dash-board',
  templateUrl: './cyber-dash-board.component.html',
  styleUrls: ['./cyber-dash-board.component.css']
})
export class CyberDashBoardComponent  {

  private title :string;
  private info : string;
  private time : object;
  private systemcheck :object;
  private getinfo :object;  
  private systemData;
  private selectEL;
  private clickswitch;
  public ispopup: boolean =false;

  constructor(private router : Router, private http: Http){
    this.http.get("assets/getCyberDashboard.json")
    .toPromise()
    .then(response =>{
      console.log(response.json());
      this.info = response.json().data.cyber_info;
      this.time = response.json().data.date;
      this.systemcheck = response.json().result.status;
      this.clickswitch = true;
    })
  }
  
  ngOnInit(){

    // setTimeout(xaa.submitWait.unblockUI,200);
    xaa.submitWait.unblockUI();
    }
    
  copyText(getinfo){
    this.title = getinfo.system_name;
    this.systemData = getinfo.system_data;
    this.selectEL = getinfo;
    this.clickswitch= true;
  }

  RecopyText(getinfo){
    this.title = getinfo.system_name;
    this.systemData = getinfo.system_data;
    this.selectEL = getinfo;
    this.clickswitch= !this.clickswitch;
  }

  popup(){

    this.ispopup =!this.ispopup;
    
  }

  stop(e){
    e.stopPropagation();
   }
 
   open_win() {
     window.open("http://rbk.intranet.hkbea.com/mobile/login.html");
 }
  
}
   
 
