import { Component } from '@angular/core'; 
import { Router } from '@angular/router';
import { Http } from '../../node_modules/@angular/http';

declare var xaa : any;

@Component({
  selector: 'app-dash-board',
  templateUrl: './dash-board.component.html',
  styleUrls: ['./dash-board.component.css']
})
export class DashBoardComponent {
  private title :string="System Monitoring Dashboard";
  private info : string;
  private time : object;
  private systemcheck :object;
  private getinfo :object;  
  private systemData :string="";
  public ispopup: boolean =false;

  constructor(private router : Router, private http: Http){
    this.http.get("assets/getDashboard.json")
    .toPromise()
    .then(response =>{
      console.log(response.json());
      this.info = response.json().data.system_info;
      this.time = response.json().data.system_time;
      this.systemcheck = response.json().result.status;
     
      
     
    })
  }

  ngOnInit(){

    // setTimeout(xaa.submitWait.unblockUI,200);
    xaa.submitWait.unblockUI();
    }
  
  copyText(getinfo){
    this.title = getinfo.system_name;
    this.systemData = getinfo.system_data;
  }

  popup(){

    this.ispopup =!this.ispopup;
    
  }
  stop(e){
    e.stopPropagation();
   }
   
   open_win() {
    window.open("http://rbk.intranet.hkbea.com/mobile/login.html");
}

}

 
 
