import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { SelectionComponent } from '../selection/selection.component';
import { CanteenComponent } from '../canteen/canteen.component';
import { DashBoardComponent } from '../dash-board/dash-board.component';
import { HOcanteenComponent } from '../hocanteen/hocanteen.component';
import { CyberDashBoardComponent } from '../cyber-dash-board/cyber-dash-board.component';

const routes: Routes= [
  { path: '', redirectTo:'/selection',pathMatch: 'full'},
  { path: 'selection', component: SelectionComponent},
  {path: 'canteen', component: CanteenComponent},
  {path: 'dash-board', component: DashBoardComponent},
  {path: 'cyber-dash-board', component: CyberDashBoardComponent},
  {path: 'hocanteen', component: HOcanteenComponent},

];

@NgModule({ 
  imports :[RouterModule.forRoot(routes,{useHash:true})],
  exports :[RouterModule]
})
export class AppRoutingModule {}