import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { Http } from '../../node_modules/@angular/http';

declare var xaa : any;

@Component({
  selector: 'app-canteen',
  templateUrl: './canteen.component.html',
  styleUrls: ['./canteen.component.css']
})
export class CanteenComponent {

  private menu;
  private getinfo: object;
  private systemData: string = "";
  private remarks: string;
  private getremarks: string = "";
  private selectedEL: string;
  private reminder: string;
  private getreminder: string = "";
  private servicetime: string;
  private switch: boolean = false;
  private today: string;
  private dateTime = new Date();
  private Day: string;
  private Month: string;
  public ispopup: boolean = false;
  private htmlmeal = [];
  private version: string;
  private versionS: string;
  private htmlsoup = [];



  constructor(private router: Router, private http: Http, ) {
    this.http.get("assets/MC5LunchMenu.json")
      .toPromise()
      .then(response => {
        console.log(response.json());
        this.menu = response.json().data.lunch_menu;
        this.remarks = response.json().data.remarks.replace(/\n/gi, "<br/>");
        this.reminder = response.json().data.reminder.replace(/\n/gi, "<br/>");
        this.servicetime = response.json().data.service_hour;
        this.switch = false;
        this.Day = this.dateTime.getDate() > 10 ? this.dateTime.getDate() + "" : "0" + this.dateTime.getDate();
        this.Month = this.dateTime.getMonth() > 9 ? (this.dateTime.getMonth() + 1) + "" : "0" + (this.dateTime.getMonth() + 1);
        this.today = this.Day + "/" + this.Month + "/" + this.dateTime.getFullYear();


        this.version = this.switch ? 'meal_e' : 'meal_c';
        this.versionS = this.switch ? 'soup_e' : 'soup_c';
        

        // console.log(list)
        for (let i = this.menu.length; i > 0; i--) {
          var _target = this.menu[i - 1];
          if (_target.date == this.today) { break }
        }
        this.selectedEL = _target;
        this.systemData = _target;
        
        for (let i = 0, data_template = _target[this.version], data_template_len = data_template.length; i < data_template_len; i++) {
          var _gg = data_template[i].replace(/(\(素\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(VEG\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(辣\))/gi, "<i class='fa fa-fire'></i>").replace(/(\(HOT\))/gi, "<i class='fa fa-fire'></i>").replace(/\n/gi, "<br/>");      
         
          this.htmlmeal.push(_gg);

        }
        this.htmlsoup = _target[this.versionS].replace(/(\(素\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(VEG\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(辣\))/gi, "<i class='fa fa-fire'></i>").replace(/(\(HOT\))/gi, "<i class='fa fa-fire'></i>").replace(/\n/gi, "<br/>");


      })
  }

  ngOnInit(){

    // setTimeout(xaa.submitWait.unblockUI,200);
    xaa.submitWait.unblockUI();
    }

  popup() {

    this.ispopup = !this.ispopup;

  }

  copyText(getinfo) {
    this.systemData = getinfo;
    this.getremarks = "";
    this.getreminder = "";
    this.selectedEL = getinfo;
    this.htmlmeal = [];
    
    for (let i = 0, data_template = getinfo[this.version], data_template_len = data_template.length; i < data_template_len; i++) {
      var _gg = data_template[i].replace(/(\(素\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(VEG\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(辣\))/gi, "<i class='fa fa-fire'></i>").replace(/(\(HOT\))/gi, "<i class='fa fa-fire'></i>").replace(/\n/gi, "<br/>");      
     
      this.htmlmeal.push(_gg);
    }
    this.htmlsoup =getinfo[this.versionS].replace(/(\(素\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(VEG\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(辣\))/gi, "<i class='fa fa-fire'></i>").replace(/(\(HOT\))/gi, "<i class='fa fa-fire'></i>").replace(/\n/gi, "<br/>");

   

  }
  copyRemarks() {
    this.systemData = "";
    this.getreminder = "";
    this.getremarks = this.remarks;
    this.selectedEL = this.remarks;

  }

  copyReminder() {
    this.systemData = "";
    this.getremarks = "";
    this.getreminder = this.reminder;
    this.selectedEL = this.reminder;

  }

  stop(e) {
    e.stopPropagation();
  }

  open_win() {
    window.open("http://rbk.intranet.hkbea.com/mobile/login.html");
  }


  changerVer() {
    this.switch = !this.switch;
    this.version = this.switch ? 'meal_e' : 'meal_c';
    this.versionS = this.switch ? 'soup_e' : 'soup_c';
    this.htmlmeal = [];
    
    for (let i = 0, data_template = this.systemData[this.version], data_template_len = data_template.length; i < data_template_len; i++) {
      var _gg = data_template[i].replace(/(\(素\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(VEG\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(辣\))/gi, "<i class='fa fa-fire'></i>").replace(/(\(HOT\))/gi, "<i class='fa fa-fire'></i>").replace(/\n/gi, "<br/>");
      this.htmlmeal.push(_gg);
    }

    this.htmlsoup = this.systemData[this.versionS].replace(/(\(素\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(VEG\))/gi, "<i class='fa fa-leaf'></i>").replace(/(\(辣\))/gi, "<i class='fa fa-fire'></i>").replace(/(\(HOT\))/gi, "<i class='fa fa-fire'></i>").replace(/\n/gi, "<br/>");


  }

}
